.footer{
  padding: 6.25em 0 3.75em;
  &__inner{
    border: $border;
  }
  &__top{
    padding: 2.25em;
    text-align: center;
  }
  &__text{
    max-width: 38.55em;
    margin: 0 auto;
    font-weight: 500;
  }
  &__logos{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__logo{
    @include center;
    padding: .3125em;
    border-top: $border;
    img{
      width: 25.25em;
      display: block;
    }
  }
  &__logo2{
    border-left: $border;
    img{
      width: 13.125em;
    }
  }
}


@media screen and (max-width: 1600px){
  .footer{
    &__logo{
      padding: .8125em;
      img{
        width: 22.5em;
      }
    }
    &__logo2{
      border-left: $border;
      img{
        width: 12.125em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.75em 0;
    &__top{
      padding: .75em 1em;
    }
    &__logo{
      padding: .625em .75em;
      img{
        width: 100%;
      }
    }
    &__logo2{
      img{
        width: 6em;
      }
    }
  }
}


