@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 3.75em;
    height: 3.75em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
        line-height: 120%;
    }
}

.m-btn-brown{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.75em;
        padding: 0 1.625em;
        span{
            font-size: 1em;
        }
    }
}
