.header{
  padding: 3.75em 0;
  position: relative;
  z-index: 4;
  &__inner{
    display: flex;
    border: $border;
  }
  &__logos{
    padding: .375em 5em;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 21.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__logo2{
    width: 11.125em;
    margin-left: 2.625em;
  }
}

.burger{
  display: none;
}

._flex{
  display: flex;
  align-items: center;
}

.menu{
  flex-grow: 1;
  &__close{
    display: none;
  }
}

.nav{
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6));
  &__link{
    @include center;
    font-weight: 500;
    color: #765f2c;
    border-top: $border;
    border-left: $border;
    &:hover,
    &._active{
      background-color: $primary;
      border-color: $primary;
      color: $white;
    }
  }
  @media screen and (min-width: 993px){
    grid-template-rows: 1fr 1fr;
    height: 100%;
    &__link{
      grid-column: span 2;
      font-size: 1.25em;
      &:nth-child(1),
      &:nth-child(2){
        grid-column: span 3;
        border-top: none;
      }
    }
  }
}


@media screen and (max-width: 1600px){
  .header{
    &__logos{
      padding: .875em 1.5em;
    }
    &__logo{
      width: 18.0625em;
    }
    &__logo2{
      width: 9.5em;
      margin-left: 2.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__inner{
      justify-content: space-between;
    }
    &__logos{
      padding: 0 1.5em;
    }
    &__logo{
      width: 8.75em;
    }
    &__logo2{
      width: 4.25em;
      margin-left: .375em;
    }
  }

  .menu{
    opacity: 0;
    transition: all .3s;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 1.25em 1em;
    &__close{
      @include center;
      width: 5.0625em;
      height: 3em;
      border: $border;
      margin: 1px 0 2em auto;
    }
  }

  .nav{
    display: block;
    &__link{
      font-size: 1em;
      height: 3em;
      border-right: $border;
      &:last-child{
        border-bottom: $border;
      }
    }
  }

  .burger{
    padding: 0 1.5em;
    height: 3em;
    min-height: 100%;
    @include center;
    border-left: $border;
    flex-shrink: 0;
    img{
      width: 2em;
    }
  }
}


