.materials {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__top {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1.125em;
  }
  &__result {
    font-size: 1.5em;
    line-height: 130%;
    font-weight: 600;
    span {
      font-weight: 700;
    }
    br {
      display: none;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 2.25em;
  }
  &__nav {
    display: flex;
    justify-content: center;
    margin-top: 3.625rem;
  }
}

.material-card {
  position: relative;
  display: flex;
  align-items: center;
  gap: 2.25em;
  padding: 1.25em 2.25em;
  border-bottom: 0.125em solid #CAB9A3;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    height: calc(100% + 0.5em);
    background: #F9F9F9;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:first-child {
    border-top: 0.125em solid #CAB9A3;
  }
  &:hover {
    color: #4F4F4F;
    &:after {
      opacity: 1;
    }
    .material-card__icon{
      background-color: $primary;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M28 4H12C10.9391 4 9.92172 4.42143 9.17157 5.17157C8.42143 5.92172 8 6.93913 8 8V40C8 42.2 9.8 44 12 44H36C37.0609 44 38.0783 43.5786 38.8284 42.8284C39.5786 42.0783 40 41.0609 40 40V16L28 4Z' stroke='%23fff' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M28 6V16H38M32 26H16M32 34H16M20 18H16' stroke='%23fff' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    }
  }
  &__icon {
    position: relative;
    z-index: 1;
    width: 8em;
    height: 8em;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F9F9F9;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M28 4H12C10.9391 4 9.92172 4.42143 9.17157 5.17157C8.42143 5.92172 8 6.93913 8 8V40C8 42.2 9.8 44 12 44H36C37.0609 44 38.0783 43.5786 38.8284 42.8284C39.5786 42.0783 40 41.0609 40 40V16L28 4Z' stroke='%234F4F4F' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M28 6V16H38M32 26H16M32 34H16M20 18H16' stroke='%234F4F4F' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    @include bgImgSize(3em);
    transition: all 0.3s;
  }
  &__text {
    position: relative;
    z-index: 1;
    font-size: 2em;
    line-height: 130%;
    font-weight: 700;
  }
}

@media screen and (max-width: 1600px) {
  .material-card {
    gap: 1.25em;
    &__icon {
      width: 6.25em;
      height: 6.25em;
      background-size: 2.5em auto;
    }
    &__text {
      font-size: 1.75em;
    }
  }
}

@media screen and (max-width: 992px) {
  .materials {
    &__result {
      font-size: 1em;
      br {
        display: block;
      }
    }
    &__nav {
      margin-top: 1.5em;
    }
  }

  .material-card {
    align-items: start;
    padding: 1.25em;
    &__icon {
      width: 2.5em;
      height: 2.5em;
      background-size: 1em auto;
    }
    &__text {
      font-size: 1em;
    }
  }
}

@media screen and (max-width: 576px) {
  .materials {
    &__cards {
      margin-top: 1.5em;
    }
  }

  .material-card {
    gap: 0.5em;
    padding: 1em 0.75em;
  }
}
