.text{
  li,
  p{
    font-size: 1.25em;
    line-height: 120%;
  }
  ul,
  p{
    @include lastM(0 0 1.25em);
  }
  b{
    font-weight: 700;
  }
  hr{
    height: 1.25em;
    opacity: 0;
    display: block;
  }
  &._list3{
    ul{
      gap: .19em;
      padding-left: .6em;
      li{
        padding-left: .8em;
        position: relative;
        &:before{
          content: 'â€¢';
          position: absolute;
          top: 0;
          left: 0;
          background-image: unset;
          width: unset;
          height: unset;
        }
      }
    }
  }
}


@media screen and (max-width: 1600px){
  .text{
    p{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    p{
      font-size: 1em;
    }
  }
}


