.doc{
  &__inner{
    display: flex;
  }
  &__col{
    flex-shrink: 0;
    margin-right: 4.625em;
  }
  &__col2{
    flex-grow: 1;
    max-width: 70.75em;
  }
  .line-title{
    &:before{
      width: 100%;
    }
  }
  .h2{
    font-weight: 700;
  }
  &__text{
    margin-top: 2.25em;
  }
}

.doc-nav{
  margin-top: 6.25em;
  display: flex;
  justify-content: space-between;
}

.doc-link{
  width: 25.75em;
  &:hover{
    color: $black;
  }
  &__top{
    display: flex;
    align-items: flex-start;
    padding: 2.25em 1.75em 1.25em 1.25em;
    img{
      width: 1.75em;
      margin-right: 1em;
    }
  }
  &__title{
    @include truncate(2);
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 700;
  }
  &._next{
    .doc-link__top{
      img{
        order: 2;
        margin: 0 0 0 1em;
      }
    }
    .back-link{
      img{
        order: 2;
        margin: 0 0 0 .625em;
        transform: rotate(-180deg);
      }
    }
  }
}


@media screen and (max-width: 1600px){

}


@media screen and (max-width: 992px){
  .doc{
    &__inner{
      display: block;
    }
    &__col{
      display: flex;
      margin: 0 0 1.5em;
    }
    &__text{
      margin-top: 1.125em;
    }
  }

  .doc-nav{
    margin-top: 3.5em;
  }

  .doc-link{
    padding: 1em .75em;
    &__top{
      padding: 0 0 .5em;
      img{
        width: 1.25em;
        margin-right: 1.625em;
      }
    }
    &__title{
      font-size: 1em;
    }
    .back-link{
      img{
        margin-right: .5em;
      }
    }
    &._next{
      .doc-link__top{
        img{
          margin: 0 .625em 0 1.625em;
        }
      }
      .back-link{
        img{
          margin: 0 0 0 .5em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .doc-nav{
    @include grid100gap(.75em);
  }

  .doc-link{
    width: 100%;
    &._next{
      order: -1;
    }
  }
}
