.flipbook-main-wrapper{
  background: $white !important;
  position: fixed;
  z-index: 10;
}

.flipbook-browser-fullscreen{
  z-index: 10 !important;
}

.flipbook-menuBottom{
  z-index: 10;
}

.flipbook-menu-center{
  border: $border;
  border-radius: 0;
  //background: rgb(81, 85, 88);
  span{
    position: relative;
    cursor: pointer;
    width: 3.75em;
    height: 3.75em;
    @include bgImgSize(1.5em);
    .fa{
      @include bgImgSize(1.5em);
      background-size: 1.5em auto !important;
      background-repeat: no-repeat !important;
      background-position: center !important;
      position: absolute;
      top: 0;
      left: 0;
      width: 100% !important;
      height: 100%;
      opacity: 0;
      box-sizing: border-box;
      margin: 0 !important;
    }
  }
  [data-name="btnZoomIn"]{
    background-image: url(../img/plus.svg);
    border-right: $border;
  }
  [data-name="btnZoomOut"]{
    opacity: 1 !important;
    background-image: url(../img/minus.svg);
    border-right: $border;
  }
  [data-name="btnExpand"]{
    background-image: unset;
    .fa{
      opacity: 1;
    }
    .fa-expand{
      background-image: url(../img/zoom.svg) !important;
    }
    .fa-compress{
      background-image: url(../img/zoom2.svg) !important;
      background-size: 2em !important;
    }
  }
  [data-name="btnAutoplay"]{
    background-image: unset;
    background-color: $primary;
    order: -1;
    .fa{
      opacity: 1;
    }
    .fa-play{
      background-image: url(../img/play.svg) !important;
    }
    .fa-pause{
      background-image: url(../img/pause.svg) !important;
      background-size: 1em auto !important;
    }
  }
  [data-name="btnSelect"],
  [data-name="btnBookmark"],
  [data-name="btnPrint"],
  [data-name="btnShare"],
  [data-name="btnThumbs"],
  [data-name="btnSound"],
  [data-name="btnDownloadPdf"],
  [data-name="btnDownloadPages"],
  [data-name="btnToc"]{
    display: none;
  }
  .fa{

  }
}

.flipbook-left-arrow,
.flipbook-right-arrow{
  margin-top: -3.75em !important;
  width: 3.75em !important;
  height: 7.5em !important;
  font-size: 1em !important;
  background-color: $primary !important;
  background-size: 2.25em auto !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
  &:hover{
    background-color: darken($primary,10%) !important;
  }
}

.flipbook-left-arrow{
  background-image: url(../img/prev.svg) !important;
}

.flipbook-right-arrow{
  background-image: url(../img/next.svg) !important;
}

.flipbook-currentPageInput{
  width: 100% !important;
  height: 100% !important;
  color: $brown !important;
  font-size: 1.25em !important;
}


.flipbook-currentPageNumber{
  font-size: 1.25em !important;
  height: 3em !important;
  padding: 0 .9em !important;
  display: flex;
  align-items: center;
  font-weight: 500;
}

.flipbook-currentPageHolder{
  background: transparent !important;
  border: $border;
  color: $brown !important;
  form{
    width: 100%;
    height: 100%;
  }
}

.flipbook-menuTop .flipbook-menu-center{
  border: none !important;
}

.book-reader{
  opacity: 0;
  pointer-events: none;
  &__close{
    position: fixed;
    top: 0;
    right: 0;
    z-index: 11;
  }
  &._open{
    opacity: 1;
    pointer-events: unset;
  }
}

.main-book{
  padding: 2.25em 0 0;
}

.book-page{
  overflow: hidden;
  &__bottom{
    margin-top: 6.25em;
  }
}

.book-download{
  display: grid;
  grid-template-columns: 45.9375em calc(100% - 45.9375em);
  &__col{
    padding: 4.875em 10em;
    background: #d3aa5e;
    color: $white;
    display: flex;
    align-items: center;
  }
  &__col2{
    padding: 2.25em 3.625em;
    border: $border;
  }
  &__nav{
    margin-top: 2.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__btn{
    padding: 0;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 1600px){
  .book-download{
    grid-template-columns: 33.125em calc(100% - 33.125em);
    &__col{
      padding: 5em 3.625em;
    }
    &__nav{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
    &__btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .book-page{
    &__bottom{
      margin-top: 3.5em;
    }
  }

  .main-book{
    padding-top: 1.5em;
  }

  .book-download{
    grid-template-columns: 100%;
    &__col{
      padding: .75em 1.625em;
    }
    &__col2{
      padding: 1.25em .75em;
    }
    &__nav{
      margin-top: .75em;
    }
    &__btn{
      ._view1{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .flipbook-left-arrow,
  .flipbook-right-arrow{
    width: 1.75em !important;
    height: 2.625em !important;
    background-size: 1.25em auto !important;
  }

  .flipbook-menu-center > span{
    font-size: .75em;
  }

  .flipbook-currentPageHolder{
    font-size: .875em;
  }

  .book-download{
    &__nav{
      grid-template-columns: 100%;
    }
    &__btn{
      padding: 0 1.625em;
      justify-content: unset;
    }
  }
}
