.gallery-page {
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 3.75em / 4));
    gap: 2.25em 1.25em;
    margin-top: 2.25em;
  }
}

.gallery-card {
  border-bottom: 0.375em solid #CAB9A3;
  padding-bottom: 2.25em;
  display: block;
  &:hover{
    color: $black;
  }
  &__img {
    @include imgCover(97%);
    overflow: hidden;
  }
  &__desc {
    max-width: 23.25em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 1.25em auto 0;
  }
  &__title {
    font-size: 2em;
    line-height: 130%;
    font-weight: 700;
  }
  &__text {
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 500;
    margin-top: 0.875em;
    @include truncate(2);
  }
}


@media screen and (max-width: 1600px) {
  .gallery-page {
    &__cards {
      gap: 1.25em;
    }
  }

  .gallery-card {
    &__title {
      font-size: 1.75em;
    }
    &__text {
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px) {
  .gallery-page {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
    }
  }

  .gallery-card {
    border-bottom: 0.125em solid #CAB9A3;
    padding-bottom: 1.25em;
    &__title {
      font-size: 1.125em;
    }
    &__text {
      font-size: 0.875em;
    }
  }
}

@media screen and (max-width: 576px) {
  .gallery-page {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 0.5em / 2));
      gap: 1.125em 0.5em;
      margin-top: 1.25em;
    }
  }
}
