.teaching,
.book-pages{
  overflow: hidden;
  &__top{
    padding-left: 46em;
  }
}

.swiper-button{
  width: 3.75em;
  height: 7.5em;
  padding: 0;
  margin: 0;
  top: 50%;
  transform: translateY(-50%);
  img{
    width: 2.25em;
  }
  &-prev{
    left: 0;
  }
  &-next{
    right: 0;
  }
}

.gallery{
  margin-top: 3.75em;
  display: grid;
  grid-template-columns: calc(100% - 18em) 16.75em;
  gap: 1.25em;
  &__big,
  &__small{
    height: 66.25em;
  }
  .swiper{
    height: 100%;
  }
  &__big{
    position: relative;
  }
  &__small{
    .swiper{
      height: calc(100% + 1.25em);
      &-slide{
        padding-bottom: 1.25em;
        height: 25%;
        &-thumb-active{
          .gallery__img:before{
            opacity: 1;
          }
        }
      }
    }
    .gallery__img{
      cursor: pointer;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: .375em solid $primary;
        transition: all .3s;
        opacity: 0;
        z-index: 2;
      }
    }
  }
  &__img{
    height: 100%;
    display: block;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}

.teaching{
  &__quotes{
    margin-top: 3.75em;
    @include grid100gap(2.25em);
  }
}

.quote-card{
  border: 1px solid #D3AA5E;
  display: grid;
  grid-template-columns: 45.9375em calc(100% - 45.9375em);
  &:nth-child(3n-1){
    border-color: #AB907D;
    .quote-card__col{
      background-color: #AB907D;
    }
    .quote-card__text{
      &:before{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.5 15L23.5 29.5H21L13.5 15L27.5 0H29.5L21.5 15Z' fill='%23AB907D'/%3E%3Cpath d='M8 15L10 29.5H7.5L0 15L14 0H16L8 15Z' fill='%23AB907D'/%3E%3C/svg%3E");
      }
      &:after{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 14.5L6 0H8.5L16 14.5L2 29.5H0L8 14.5Z' fill='%23AB907D'/%3E%3Cpath d='M21.5 14.5L19.5 0H22L29.5 14.5L15.5 29.5H13.5L21.5 14.5Z' fill='%23AB907D'/%3E%3C/svg%3E");
      }
    }
  }
  &:nth-child(3n){
    border-color: #CBAF95;
    .quote-card__col{
      background-color: #CBAF95;
    }
    .quote-card__text{
      &:before{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.5 15L23.5 29.5H21L13.5 15L27.5 0H29.5L21.5 15Z' fill='%23CBAF95'/%3E%3Cpath d='M8 15L10 29.5H7.5L0 15L14 0H16L8 15Z' fill='%23CBAF95'/%3E%3C/svg%3E");
      }
      &:after{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 14.5L6 0H8.5L16 14.5L2 29.5H0L8 14.5Z' fill='%23CBAF95'/%3E%3Cpath d='M21.5 14.5L19.5 0H22L29.5 14.5L15.5 29.5H13.5L21.5 14.5Z' fill='%23CBAF95'/%3E%3C/svg%3E");
      }
    }
  }
  &__col{
    background-color: #D3AA5E;
    padding: 3.75em 11.875em;
    color: $white;
    display: flex;
    align-items: center;
  }
  &__col2{
    padding: 2.25em 6em;
  }
  &__title{
    font-weight: 600;
  }
  &__text{
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 1.875em;
      height: 1.875em;
      @include bgImgSize(100%);
    }
    &:before{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.5 15L23.5 29.5H21L13.5 15L27.5 0H29.5L21.5 15Z' fill='%23D3AA5E'/%3E%3Cpath d='M8 15L10 29.5H7.5L0 15L14 0H16L8 15Z' fill='%23D3AA5E'/%3E%3C/svg%3E");
      top: 0;
      right: 100%;
      margin-right: .9375em;
    }
    &:after{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 14.5L6 0H8.5L16 14.5L2 29.5H0L8 14.5Z' fill='%23D3AA5E'/%3E%3Cpath d='M21.5 14.5L19.5 0H22L29.5 14.5L15.5 29.5H13.5L21.5 14.5Z' fill='%23D3AA5E'/%3E%3C/svg%3E");
      bottom: 0;
      left: 100%;
      margin-left: .9375em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__link{
    font-weight: 700;
    text-decoration: underline;
  }
}


@media screen and (max-width: 1600px){
  .teaching,
  .book-pages{
    &__top{
      padding-left: 33em;
    }
  }

  .gallery{
    grid-template-columns: calc(100% - 13.25em) 12em;
    &__big,
    &__small{
      height: 48.5em;
    }
  }

  .quote-card{
    grid-template-columns: 33.125em calc(100% - 33.125em);
    &__col{
      padding: 4.5em 5.75em;
    }
    &__col2{
      padding: 2.25em 4.375em;
    }
  }
}


@media screen and (max-width: 992px){
  .teaching,
  .book-pages{
    &__top{
      padding-left: 0;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: .5em;
    margin-top: 1.75em;
    &__big,
    &__small{
      height: unset;
    }
    &__big{
      .gallery__img{
        padding-bottom: 81.3%;
      }
    }
    &__small{
      .swiper{
        height: 100%;
        width: calc(100% + .5em);
        &-slide{
          height: 100%;
          width: 25%;
          padding: 0 .5em 0 0;
        }
      }
    }
    &__img{
      height: auto;
      @include imgCover(100%);
    }
  }

  .teaching{
    &__quotes{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
      margin-top: 1.5em;
    }
  }

  .quote-card{
    grid-template-columns: 100%;
    &__col{
      padding: .75em 1.25em;
    }
    &__col2{
      padding: 1.25em .75em;
    }
    &__text{
      &:before,
      &:after{
        position: static;
        width: 1.375em;
        height: 1.375em;
        display: block;
      }
      &:before{
        margin-bottom: .75em;
      }
      &:after{
        margin: .75em 0 0 auto;
      }
    }
    &__nav{
      margin-top: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .swiper-button{
    width: 1.75em;
    height: 2.75em;
    img{
      width: 1.25em;
    }
  }

  .gallery{
    &__small{
      .gallery__img{
        &:before{
          border-width: 2px;
        }
      }
    }
  }

  .teaching{
    &__quotes{
      grid-template-columns: 100%;
    }
  }
}
