.m-section{
  padding: 6.25em 0;
  &--first{
    padding-top: 2.5em;
  }
}

.main,
.about{
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 40em calc(100% - 46.25em);
    gap: 6.25em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    margin-top: 2em;
  }
}

.line-title{
  position: relative;
  padding-bottom: 2.25em;
  &:before{
    content: '';
    position: absolute;
    height: .75em;
    width: 500em;
    background-color: $secondary;
    left: 0;
    bottom: 0;
  }
  &._type2{
    display: inline-flex;
    &:before{
      left: unset;
      right: 0;
    }
  }
}

.main{
  padding-top: 8.75em;
  &__inner{
    grid-template-columns: 39em calc(100% - 45.25em);
  }
  &__text{
    max-width: 60.625em;
  }
  &__nav{
    display: flex;
    margin-top: 2.25em;
  }
}


@media screen and (max-width: 1600px){
  .about{
    &__inner{
      grid-template-columns: 33.125em calc(100% - 39.375em);
    }
  }


  .main{
    &__inner{
      grid-template-columns: 26.875em calc(100% - 33.125em);
    }
    &__text{
      max-width: 43.75em;
    }
  }
}



@media screen and (max-width: 992px){
  .m-section{
    padding: 1.75em 0;
    &--first{
      padding-top: .75em;
    }
  }

  .main,
  .about{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    &__text{
      margin-top: 1.5em;
    }
  }

  .line-title{
    padding-bottom: 1.25em;
    &:before{
      height: .375em;
    }
  }

  .main{
    padding-top: .75em;
    &__inner{
      grid-template-columns: 16em calc(100% - 17.5em);
    }
    &__nav{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main,
  .about{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .main{
    &__img{
      width: 16em;
      margin: 0 auto;
    }
  }
}
