.back-link{
  img{
    width: 1.5em;
    margin-right: .625em;
  }
}

.images{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__count{
    font-weight: 700;
  }
  .h2{
    text-transform: unset;
    font-weight: 700;
  }
  &__list{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(12,calc(100% / 12 - 11 * 1.25em / 12));
    gap: 1.25em;
    grid-auto-flow: dense;
  }
}

.images-slider{
  margin-top: 2.25em;
  overflow: hidden;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-slide{
      padding-right: 1em;
    }
    &-button{
      &.swiper-button-disabled{
        pointer-events: unset;
      }
    }
  }

}

.image-item{
  position: relative;
  &__img{
    @include imgCover(60.3%);
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      backdrop-filter: blur(20px);
      background-color: rgba($white, 0.25);
      background-image: url(../img/full.svg);
      @include bgImgSize(1.5em);
      width: 3.75em;
      height: 3.75em;
    }
  }
  &__text{
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    padding: 1.25em;
    width: 63em;
    max-width: 100%;
    color: $white;
    backdrop-filter: blur(20px);
    background-color: rgba($white,.25);
  }
}

.image-item2{
  position: relative;
  grid-column: span 2;
  &._big{
    grid-column: span 3;
  }
  &._big2{
    grid-column: span 4;
  }
  &__upload{
    height: 16.25em;
    background-color: #e7e6e6;
    background-image: url(../img/camera.svg);
    @include bgImgSize(2.25em);
  }
  &__uploaded{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: .5em solid $primary;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
}


@media screen and (max-width: 1600px){
  .images{
    &__list{
      grid-template-columns: repeat(12,calc(100% / 12 - 11em / 12));
      gap: 1em;
      margin-top: 1em;
    }
  }

  .image-item2{
    &__upload{
      height: 12em;
    }
  }
}


@media screen and (max-width: 992px){
  .back-link{
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }

  .images{
    &__top{
      flex-wrap: wrap;
    }
    .h2{
      width: 100%;
      order: 2;
      margin-top: 1em;
      text-align: center;
    }
    &__list{
      margin-top: .5em;
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      gap: .5em;
    }
  }

  .images-slider{
    margin-top: 1.5em;
    .swiper{
      &-button{
        top: 13.85em;
      }
    }
  }

  .image-item{
    &__img{
      &:after{
        display: none;
      }
    }
    &__text{
      padding: 1em .75em;
      background: #f5f5f5;
      color: $black;
      position: static;
      margin-top: .25em;
      transform: unset;
    }
  }

  .image-item2{
    grid-column: unset;
    &._big,
    &._big2{
      grid-column: span 2;
    }
  }
}


@media screen and (max-width: 576px){
  .images-slider{
    .swiper{
      &-button{
        top: 6.45em;
      }
    }
  }

  .image-item2{
    &__upload{
      height: 5.375em;
      background-size: 1.25em auto;
    }
    &__uploaded{
      border-width: .125em;
    }
  }
}
